
.vm-slide-box {
  position: relative;
  height: 52px;
  overflow: hidden;
  background-color: #ddd;
  box-shadow: 0 0 3px #999;
}
.vm-slide-box .vm-slide-box-placeholder {
    display: block;
    width: 100%;
    height: 52px;
    font-size: 16px;
    line-height: 52px;
    color: #aaa;
    text-align: center;
    background: gradient(linear, left top, right top, color-stop(0, #4d4d4d), color-stop(0.4, #4d4d4d), color-stop(0.5, #fff), color-stop(0.6, #4d4d4d), color-stop(1, #4d4d4d));
}
@-webkit-keyframes slidetounlock {
0% {
    background-position: -200px 0;
}
100% {
    background-position: 200px 0;
}
}
@keyframes slidetounlock {
0% {
    background-position: -200px 0;
}
100% {
    background-position: 200px 0;
}
}
.vm-slide-box .vm-slide-box-track {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}
.vm-slide-box .vm-slide-box-track .track-background {
      width: 100%;
      height: 52px;
      font-size: 16px;
      line-height: 52px;
      color: #fff;
      text-align: center;
}
.vm-slide-box .vm-slide-box-track .track-btn {
      position: absolute;
      top: 0;
      right: 0;
      display: -webkit-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      width: 52px;
      height: 52px;
      cursor: pointer;
      background: #fff;
      opacity: 1;
      -webkit-box-align: center;
      -webkit-align-items: center;
          -ms-flex-align: center;
              align-items: center;
      -webkit-box-pack: center;
      -webkit-justify-content: center;
          -ms-flex-pack: center;
              justify-content: center;
}
.vm-slide-box .vm-slide-box-track .track-btn .icon-arrow-right {
        width: 20px;
        height: 20px;
        background: #fff url("images/arrow.png") no-repeat center center/20px 20px;
}
.vm-slide-box .vm-slide-box-track .track-btn .icon-ok {
        width: 20px;
        height: 20px;
        background: #fff url("images/ok.png") no-repeat center center/20px 20px;
}
.vm-slide-box .vm-slide-box-track .track-btn .icon-error {
        width: 20px;
        height: 20px;
        background: #fff url("images/error.png") no-repeat center center/20px 20px;
}
.vm-slide-box .vm-slide-box-track .track-btn .icon-refresh {
        width: 20px;
        height: 20px;
        background: #fff url("images/refresh.png") no-repeat center center/20px 20px;
        -webkit-animation: revolve 2s linear infinite;
                animation: revolve 2s linear infinite;
}
@-webkit-keyframes revolve {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes revolve {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
.vm-slide-box[state="inactive"] .vm-slide-box-track .track-background,
.vm-slide-box[state="sliding"] .vm-slide-box-track .track-background,
.vm-slide-box[state="checking"] .vm-slide-box-track .track-background,
.vm-slide-box[state="cancelling"] .vm-slide-box-track .track-background,
.vm-slide-box[state="completing"] .vm-slide-box-track .track-background {
  background-color: #78c430;
}
.vm-slide-box[state="failing"] .vm-slide-box-track .track-background {
  background-color: #ef4644;
}
.vm-slide-box[state="cancelling"]::after {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  pointer-events: none;
  content: "";
}
.vm-slide-box[state="cancelling"] .vm-slide-box-track {
  -webkit-transition: -webkit-transform ease 500ms;
  transition: -webkit-transform ease 500ms;
  transition: transform ease 500ms;
  transition: transform ease 500ms, -webkit-transform ease 500ms;
  will-change: transform;
}



