
.vm-feedback.feedback__inputBox {
  border-bottom: 1px solid #f2f2f2;
  width: 100%;
  background: #fff;
  box-sizing: border-box;
  margin-bottom: 10px;
}
.vm-feedback.feedback__inputBox .feedback__inputBox--textareaBox {
    height: auto;
    width: 100%;
    padding: 10px;
    background: #fff;
}
.vm-feedback.feedback__inputBox .feedback__text {
    color: #8b8b8b;
    font-size: 14px;
    margin: 0;
    text-align: right;
    padding: 0 10px;
}
.vm-feedback.feedback__inputBox .feedback__inputBox--images {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: start;
    -webkit-justify-content: flex-start;
        -ms-flex-pack: start;
            justify-content: flex-start;
    -webkit-align-content: flex-start;
        -ms-flex-line-pack: start;
            align-content: flex-start;
    -webkit-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
    padding: 10px;
}
.vm-feedback.feedback__inputBox .feedback__inputBox--images .image {
      width: 60px;
      height: 60px;
      margin-right: 10px;
      margin-bottom: 10px;
      box-sizing: border-box;
      /*overflow: hidden;*/
      position: relative;
      display: -webkit-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-flex: 0;
      -webkit-flex: 0 0 auto;
          -ms-flex: 0 0 auto;
              flex: 0 0 auto;
      -webkit-box-pack: center;
      -webkit-justify-content: center;
          -ms-flex-pack: center;
              justify-content: center;
      -webkit-box-align: center;
      -webkit-align-items: center;
          -ms-flex-align: center;
              align-items: center;
      border: 1px solid #eee;
      background-repeat: no-repeat;
      background-color: #eee;
      background-position: center center;
      background-size: cover;
}
.vm-feedback.feedback__inputBox .feedback__inputBox--images .image .file {
        display: none;
}
.vm-feedback.feedback__inputBox .feedback__inputBox--images .image img {
        width: 100%;
        min-height: 100%;
}
.vm-feedback.feedback__inputBox .feedback__inputBox--images .preview {
      height: 100%;
      width: 100%;
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
}
.vm-feedback.feedback__inputBox .feedback__inputBox--images .delete {
      position: absolute;
      height: 25px;
      width: 25px;
      top: -5px;
      right: -5px;
      z-index: 2;
}
.vm-feedback.feedback__inputBox .feedback__inputBox--images .delete:before {
        content: '';
        position: absolute;
        height: 16px;
        width: 16px;
        background: red;
        border-radius: 100%;
        top: 0;
        right: 0;
        z-index: 3;
}
.vm-feedback.feedback__inputBox .feedback__inputBox--images .delete::after {
        content: '';
        height: 1px;
        width: 10px;
        position: absolute;
        background: #fff;
        top: 7px;
        right: 3px;
        z-index: 4;
}
.vm-feedback.feedback__inputBox .feedback__inputBox--images .empty {
      border: 1px dashed #8c8c8c;
      background: #f3f3f3;
      position: relative;
}
.vm-feedback.feedback__inputBox .feedback__inputBox--images .empty .file {
        display: block !important;
        width: 100%;
        height: 100%;
        opacity: 0;
}
.vm-feedback.feedback__inputBox .feedback__inputBox--images .empty::before, .vm-feedback.feedback__inputBox .feedback__inputBox--images .empty::after {
        background: #dadada;
        left: 50%;
        top: 50%;
        position: absolute;
        content: '';
}
.vm-feedback.feedback__inputBox .feedback__inputBox--images .empty::after {
        margin-left: -1px;
        margin-top: -15px;
        height: 30px;
        width: 2px;
}
.vm-feedback.feedback__inputBox .feedback__inputBox--images .empty::before {
        margin-left: -15px;
        margin-top: -1px;
        width: 30px;
        height: 2px;
}



