
.vm-scroll-segment {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.vm-scroll-segment .scroll-segment-outer {
    position: relative;
    height: -webkit-calc(100% + 10px);
    height: calc(100% + 10px);
    overflow-x: scroll;
    overflow-y: hidden;
    -webkit-overflow-scrolling: touch;
}
.vm-scroll-segment .scroll-segment-outer::-webkit-scrollbar {
      display: none;
}
.vm-scroll-segment .scroll-segment-wrap {
    display: -webkit-inline-box;
    display: -webkit-inline-flex;
    display: -ms-inline-flexbox;
    display: inline-flex;
    height: -webkit-calc(100% - 10px);
    height: calc(100% - 10px);
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -webkit-flex-direction: row;
        -ms-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: nowrap;
        -ms-flex-wrap: nowrap;
            flex-wrap: nowrap;
}



